.like {

    &__title{
        font-size: 60px;
        font-weight: 400;
        text-align: center;
        padding: 50px 0;
        border-bottom: 2px solid #555555;
        margin-bottom: 70px;

        @media(max-width: 550px) {
            font-size: 30px;
        }
    }

    &__name {
        font-size: 30px;
        font-weight: 400;
        line-height: 36px;
        text-align: center;
        padding: 20px 0;
    }

    &__price {
        font-size: 30px;
        font-weight: 400;
        line-height: 36px;
        text-align: center;
        font-weight: bold;
    }

    &__block {
        width: 534px;
        position: relative;

        @media(max-width: 1750px) {
            margin: 20px 10px;
        }
    }

    &__img {
        @media(max-width: 550px) {
            width: 100%;
        }
    }

    &__top{
        display: flex;
        justify-content: space-between;
        padding: 20px;
        position: absolute;
        width: 100%;

        i {
            font-size: 30px;
        }

    }

    &__blocks {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        @media(max-width: 1750px) {
            justify-content: center;
        }
    }

}