.main {

    &__flex{
        display: flex;
        flex-wrap: wrap;
    }

    &__block {
        width: 60%;
        display: flex;

        @media (max-width: 1700px) {
            width: 100%;
            justify-content: center;
        }

        @media (max-width: 720px) {
            flex-direction: column;
        }
    }

    &__fullimg {
        width: 685px;
        height: 838px;
        margin-right: 10px;

        @media(max-width: 1000px) {
            height: auto;
            width: 100%;
        }
    }

    &__fullimg img {
        width: 100%;
        height: 100%;

        @media(max-width: 1000px) {
            height: auto;
            width: 100%;
        }
    }

    &__smallimg {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 838px;

        @media(max-width: 1000px) {
            height: auto;
            justify-content: space-around;
        }

        @media(max-width: 720px) {
            flex-direction: row;
            justify-content: space-between;
        }

    }

    &__smallimg img{
        width: 241px;
        height: 263px;
        cursor: pointer;

        @media(max-width: 1000px) {
            height: auto;
            width: 100%;
        }

        @media(max-width: 720px) {
            width: 30%;
        }
    }
}

.aside {
    width: 40%;

    @media (max-width: 1700px) {
        width: 100%;
        margin-top: 30px;
    }

    &__title {
        color: var(--gray);
        font-size: 48px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
    }

    &__capt {
        color: var(--gray);
        font-size: 48px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 30px;
    }
    
    &__razm{
        color: var(--gray);
        font-size: 24px;
        font-weight: 400;
        text-decoration: underline;
    }
    
    &__message {
        color: var(--gray);
        font-size: 24px;
        font-weight: bold;
        text-decoration: underline;
    }

    &__table {
        display: flex;
        justify-content: space-between;
        padding-bottom: 20px;
    }

    &__add {
        width: 492px;
        height: 98px;
        background: var(--gray);
        color: var(--white);
        font-size: 30px;
        font-weight: 300;
        line-height: 30px;
    }

    &__desc-title {
        font-size: 24px;
        font-weight: 400;
        line-height: 30px;
        color: var(--gray);
        border-bottom: 2px solid var(--gray);
        padding-bottom: 10px;
        margin-bottom: 10px;
    }

    &__desc-text {
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        color: var(--gray);
    }

    &__link-details {
        font-size: 18px;
        font-weight: bold;
        color: var(--gray);
        padding: 10px 0 20px;
        display: block;
    }

    &__share-text {
        text-transform: uppercase;
        font-size: 24px;
        font-weight: bold;
        padding-right: 25px;

    }

    &__fieldset {
        border: none;
    }

    &__block {
        width: 640px;

        @media (max-width: 1700px) {
            margin: 0px auto;
        }

        @media (max-width: 700px) {
            width: 100%;
        }
    }

    &__btnblock {
        margin-bottom: 50px;
        display: flex;

        i {
            width: 106px;
            height: 98px;
            background-color: #d4d4d4;
            color: var(--gray);
            font-size: 50px;
            text-align: center;
            line-height: 100px;
            margin-left: 10px;
        }
    }
}

.aside__select {
    margin-bottom: 40px;
    cursor: pointer;
    position: relative;
    transition: all 1s;

    &-wrap:hover {
        font-weight: bold;
    }

    &-wrap:hover:after{
        opacity: 1;
    }


    &-wrapper {
        display: none;
        position: absolute;
        z-index: 2;
        background: var(--white);
        width: 100%;
    }

    &-wrap {
        width: 625px;
        height: 98px;
        border: 2px solid #acacac;
        text-align: center;
        color: #1e1e1e;
        font-family: Futura;
        font-size: 30px;
        font-weight: 300;
        line-height: 95px;
        position: relative;

        @media(max-width: 700px) {
            width: 100%;
        }

        &:after {
            content: "";
            display: block;
            width: 30px;
            height: 25px;
            background-size: 30px 25px;
            background-image: url("../img/arr.svg");
            background-repeat: no-repeat;
            position: absolute;
            top: 40px;
            right: 40px;
            opacity: .5;
        }
    }


    &-wrapp {
        width: 625px;
        height: 98px;
        border: 2px solid #acacac;
        text-align: center;
        color: #1e1e1e;
        font-family: Futura;
        font-size: 30px;
        font-weight: 300;
        line-height: 95px;
        position: relative;

        @media(max-width: 700px) {
            width: 100%;
        }

        &:hover {
            font-weight: bold;
        }
    }

    &-color {
        width: 45px;
        height: 45px;
        background: #464646;
        position: absolute;
        border-radius: 50%;
        top: 30px;
        left: 40px;

        @media(max-width: 420px) {
            left: 10px;
        }
    }

    &-col {

        @media(max-width: 420px) {
            font-size: 18px;
        }
    }
}

