.footer {

    &__wrap {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }

    &__title {
        color: #191919;
        font-size: 24px;
        font-weight: bold;
        line-height: 36px;
        padding: 20px 0;

        @media(max-width: 440px) {
            font-size: 18px;
        }
    }

    &__link {
        color: #191919;
        font-size: 18px;
        font-weight: 400;
        line-height: 30px;
        text-transform: uppercase;
        display: block;
        text-decoration: none;
    }

    &__capir {
        text-align: center;
        padding: 20px 0;
        border-top: 2px solid #555555;

    }

    &__cosial-icons i {
        width: 31px;
        height: 31px;
        background: black;
        color: var(--white);
        text-align: center;
        line-height: 30px;
        border-radius: 50%;
        margin: 3px;
    }

    &__cosial-icons {
        display: inline-block;
    }

    &__item {

        @media(max-width: 1250px) {
            width: 50%;
        }
    }
}