@import "../css/header.css";
@import "../css/main.css";
@import "../css/like.css";
@import "../css/footer.css";

@font-face {
    font-family: ProximaNova;
    src: url("../fonts/ProximaNova-Regular.ttf") format("ttf");
    font-style: normal;
    font-weight: 400;
}

@font-face {
    font-family: Futura;
    src: url("../fonts/FuturaLightC.woff") format("woff");
    font-style: normal;
    font-weight: 400;
}

:root {
    --default: ProximaNova , sans-serif;
    --gray: #292929;
    --gray-light: #282828;
    --blue: #56ccf2;
    --yellow: #f2c94c;
    --white: #fff;
    --black: #000;
    --gold: #c8a35f;
    --dark: #2f343a;
    --white: #ffffff;

}

*, ::after, ::before {
    box-sizing: border-box;
}

body, html {
    margin: 0;
    padding: 0;
    font-family: var(--default);
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--gray);
    background-color: var(--white);
}

.container {
    max-width: 1920px;
    padding: 0 10px;
    margin: 0 auto;

    @media (max-width: 1800px) {
        width: 95%;
    }
}



