.topline {

    &__for {
        @media(max-width: 1250px) {
            display: none;
        }
    }

    &__acces {
        @media(max-width: 1250px) {
            display: none;
        }
    }

    &__item {

        @media(max-width: 700px) {
            display: none;
        }
    }

    &__account {

        @media(max-width: 480px) {
            display: none;
        }
    }

    &__items{
        display: flex;
        text-transform: uppercase;
        align-items: center;
        justify-content: space-between;

        @media(max-width: 480px) {
            justify-content: center;
        }
    }

    &__location {
        color: var(--gray-light);
        font-size: 18px;
        font-weight: 400;
        line-height: 36px;
    }

    &__location:first-child {
        margin-right: 10px;
    }
    
    &__location:after {
        content: url("../img/arr.svg");
        background-position: center;
        position: relative;
        top: -2px;
        right: -2px;
        margin: 5px;
    }

    &__new {
        color: var(--gray);
        color: #711c02;
        font-weight: bold;
        margin-right: 30px;
        font-size: 24px;
    }

    &__voman {
        color: var(--gray);
        font-size: 24px;
    }

    &__aksec {
        color: var(--gray);
        font-size: 24px;
        margin-right: 30px;
    }

    &__sale {
        color: var(--gray);
        font-size: 24px;
        color: #711c02;
        font-weight: bold;
    }

    &__exit {
        color: var(--gray);
        font-size: 18px;
        font-weight: 400;
        line-height: 36px;
        margin-right: 10px;
    }

    &__market {
        color: var(--gray);
        font-size: 18px;
        font-weight: 400;
        line-height: 36px;
    }

    &__market:before {
        content: url("../img/market.svg");
        margin: 5px;
    }

    a {
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

}

.menu {

    &__items {
        text-align: center;
        padding: 30px 0;
        margin-bottom: 50px;
    }

    &__link {
        color: #585656;
        font-size: 30px;
        font-weight: 400;
        line-height: 30px;
        letter-spacing: .3px;
        text-decoration: none;
    }

    &__link:not(:last-child):after {
        content: "/";
        margin: 0 10px;
    }
}